* {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

body {
    padding: 2em;
}

input{
    width: 100%;
    font-size: 1.2rem;
    color: gray;
}


.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 0 10px 0 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}
